/*------------------------------
  510 PX
  ------------------------------
  */
@media screen and (max-width: 510px){
    .main-first-container {
        width: 100%;
        height: 400px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    .wave-top {
        width: 100%;
        height: auto;
        position: relative;
        bottom: -80%;
    }

    .main-second-container {
        padding-top: 5%;
        height: 40vh;
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: center;
    }

    .wave-bottom{
        width: 100%;
        height: auto;
        position: relative;
        bottom: -60%;
    }

    .main-third-container{
        height: 100px;
    }
}


/*------------------------------
  390 PX
  ------------------------------
  */
@media screen and (max-width: 390px) {
    .wave-top {
        width: 100%;
        height: auto;
        position: relative;
        bottom: -85%;
    }

    .wave-bottom{
        width: 100%;
        height: auto;
        position: relative;
        bottom: -50%;
    }
}


/*------------------------------
  380 PX
  ------------------------------
  */
@media screen and (max-width: 380px) {
    .wave-bottom{
        width: 100%;
        height: auto;
        position: relative;
        bottom: -50%;
    }
}


/*------------------------------
  370 PX
  ------------------------------
  */
@media screen and (max-width: 370px) {
    .main-second-container{
        color: brown;
    }
}